// Imports
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { selectWishItems } from "../../redux/selectors/wishlist";
import { ADD_TO_WISHLIST } from "../../redux/types/types";
import { useTranslation } from "react-i18next";

// Redux Data
const mapState = (state) => ({
	wishItems: selectWishItems(state),
});

function Product({ item, cls, size, type, orderPrice, orderQuantity }) {
	const { t } = useTranslation(["product"]);
	// Redux Data
	const { wishItems } = useSelector(mapState);
	const dispatch = useDispatch();

	const [checkWishItem, setWishCheckItem] = useState(null);

	const handleAddToWishlist = () => {
		dispatch({ type: ADD_TO_WISHLIST, payload: item });
	};
	// Checking if item in card or not
	useEffect(() => {
		setWishCheckItem(wishItems.find((wishItem) => wishItem.id === item.id));
	}, [wishItems, item]);

	return (
		<div
			className={`border-0  ${cls && cls}`}
			style={{
				borderRadius: "20px",
			}}
		>
			{/* Product Top Start */}
			<div
				className="border-bottom position-relative bg-white"
				style={{ borderRadius: "20px" }}
			>
				{/* Like button */}
				{type ? (
					""
				) : (
					<button
						className={`badge btn bg-transparent like-btn ${
							checkWishItem ? "text-danger" : "text-grayy"
						}  position-absolute`}
						style={{
							top: "2%",
							right: "3%",
							borderRadius: "20px",
						}}
						onClick={() => {
							handleAddToWishlist();
						}}
					>
						<i className={`bi bi-heart${checkWishItem ? "-fill" : ""} h4`}></i>
					</button>
				)}
				{type && (
					<span
						role="button"
						class="badge bg-primary truncate-overflow-1 text-white like-btn  position-absolute"
						style={{
							fontSize: "14px",
							borderRadius: "20px",
							top: "4%",
							left: "8%",
						}}
					>
						{orderQuantity} / {orderPrice} $
					</span>
				)}
				{/* Image of a Product */}
				{type === "order" ? (
					<img
						alt="Product"
						className="img-fluid"
						style={{ borderRadius: "20px" }}
						src={item?.picture_middle}
					/>
				) : (
					<Link
						to={`/product/${item.slug}/${item.id}`}
						className="text-decoration-none text-dark"
					>
						<img
							alt="Product"
							className="img-fluid"
							style={{ borderRadius: "20px" }}
							src={item?.picture_middle}
						/>
					</Link>
				)}
			</div>
			{/* Product Top End */}

			{/* Product Info Start */}
			{type === "order" ? (
				<div
					className="d-flex justify-content-between flex-column mx-auto pt-2 "
					style={{ width: "85%", lineHeight: 1.3 }}
				>
					<span className="text-dark text-decoration-none truncate-overflow-2 mb-1">
						{item.new && (
							<span class="badge bg-success me-1">{t("product:new")}</span>
						)}
						{size ? `${size} - ` : ""} {item?.name} ({item?.category.name}
						{item.brand && `- ${item.brand.name}`})
					</span>

					<div className="price-wrap mt-1 mb-2 ">
						<div className="price h4  ">
							{item?.new_price}
							<span className="ps-1 small font-weight-light">
								<small>TMT</small>
							</span>
						</div>
						{item.discount && (
							<div className="truncate-overflow-1">
								<del className="text-muted">
									<small>{item?.old_price} TMT</small>
								</del>
								<span class="badge bg-danger ms-2">-{item?.discount}% off</span>
							</div>
						)}
					</div>
				</div>
			) : (
				<Link
					to={`/product/${item.slug}/${item.id}`}
					className="text-decoration-none text-dark"
				>
					<div
						className="d-flex justify-content-between flex-column mx-auto pt-2 "
						style={{ width: "85%", lineHeight: 1.3 }}
					>
						<span className="text-dark text-decoration-none truncate-overflow-2 mb-1">
							{item.new && (
								<span class="badge bg-success me-1">{t("product:new")}</span>
							)}
							{size ? `${size} - ` : ""} {item?.name} ({item?.category.name}
							{item.brand && `- ${item.brand.name}`})
						</span>

						<div className="price-wrap mt-1 mb-2 ">
							<div className="price h4  ">
								{item?.new_price}
								<span className="ps-1 small font-weight-light">
									<small>TMT</small>
								</span>
							</div>
							{item.discount && (
								<div className="truncate-overflow-1">
									<del className="text-muted">
										<small>{item?.old_price} TMT</small>
									</del>
									<span class="badge bg-danger ms-2">
										-{item?.discount}% off
									</span>
								</div>
							)}
						</div>
					</div>
				</Link>
			)}
			{/* Product Info End */}
		</div>
	);
}

export default Product;
