import React, { useState, useEffect } from "react";
// Components
import Product from "../Product/Product";
// Slider Data
import { productOptions } from "./SliderOptions.jsx";
// Redux
import { useDispatch } from "react-redux";
import { get_home_products } from "../../redux/actions/products";
// Swiper
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper-bundle.min.css";
import { useTranslation } from "react-i18next";
import ProductLoading from "../Product/ProductLoading";

function ProductSlider({ confi, gotProducts }) {
	// redux
	const dispatch = useDispatch();

	const [products, setProducts] = useState(null);
	const { i18n } = useTranslation();
	// fetch
	useEffect(() => {
		if (!gotProducts) {
			dispatch(get_home_products(confi, i18n.language)).then((result) => {
				setProducts(result);
			});
		} else {
			setProducts(gotProducts);
		}
		return () => {
			setProducts(null);
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [gotProducts, confi, i18n.language]);

	if (Array.isArray(products) && products.length >= 1) {
		return (
			<Swiper {...productOptions}>
				{products.map((product) => {
					return (
						<SwiperSlide>
							<Product item={product} />
						</SwiperSlide>
					);
				})}
			</Swiper>
		);
	} else {
		return (
			<Swiper {...productOptions}>
				<SwiperSlide>
					<ProductLoading />
				</SwiperSlide>
				<SwiperSlide>
					<ProductLoading />
				</SwiperSlide>
				<SwiperSlide>
					<ProductLoading />
				</SwiperSlide>
				<SwiperSlide>
					<ProductLoading />
				</SwiperSlide>
				<SwiperSlide>
					<ProductLoading />
				</SwiperSlide>
			</Swiper>
		);
	}
}

export default ProductSlider;
